/**
 * JSON
 * @param value1 : any
 * @param value2 : any
 * @returns boolean
 *
 *
 * 
 * - 2    false
 * -  JSON.stringify() 
 */
export function isEqualOfJSON(value1, value2) {
  return JSON.stringify(value1) == JSON.stringify(value2);
}




