/**
 * multipleLoop(option)=> stopLoop()
 * 
 * @param option : {loopCall,complete,stepComplete,thisValue,total,step,delay}   
 * @property option.loopCall : (index,stepCount,total)=>stopInfo : any    index : number   index0 stepCount : number   total: number   stopInfo : any 
 * @property option.stepComplete  : (index,stepCount,total)=>stopInfo : any    index : number   index0 stepCount : number   total: number   stopInfo : any 
 * @property option.complete : (stopInfo,index,stepCount,total)=>Void    stopInfo : any   index : number   index-1  stepCount : number   total: number 
 * @property option.thisValue ? : any   loopCallcompletestepComplete this
 * @property option.total ? : number    Number.POSITIVE_INFINITY ; 
 * @property option.step ? : number      50  
 * @property option.delay ? : Timestamp    0  
 * @returns stopLoop : (stopInfo)=>Void      stopInfo : any 
 */


export function multipleLoop({loopCall,complete,stepComplete,thisValue,total=Number.POSITIVE_INFINITY,step = 50,delay = 0}) {


  let index = 0;
  let stepCount = 0;   //


  /**
   * 
   *
   * stop
   * -     complete 
   */
  let stop = false;    //
  function setStop(newValue){
    if (newValue){
      stop = newValue;

      if (complete) {
        complete.call(thisValue,stop,index,stepCount,total);
      }
    }
  }


  /**
   * 
   * @returns boolean
   *
   * 
   * - 
   * -     complete 
   */
  function loop(){
    let needLoop = index < total;

    if (!needLoop && complete) {
      complete.call(thisValue,false,index-1,stepCount,total);
    }
    return needLoop
  }


  let timeoutId = null;






  /**
   * 
   */
  function atuoSingleLoop() {
    stepCount++;

    let singleTotal = Math.min(index + step,total);

    function singleLoop(){return index < singleTotal}

    while (singleLoop() && !stop){
      setStop(loopCall.call(thisValue,index,stepCount,total));
      index++;
    }

    if (stepComplete){
      setStop(stepComplete.call(thisValue,index-1,stepCount,total));
    }


    if (loop() && !stop){
      timeoutId = setTimeout(atuoSingleLoop,delay);
    }
  }


  /**
   * 
   */
  function stopLoop(stopInfo) {
    if (!stopInfo){
      stopInfo = true;
    }

    clearTimeout(timeoutId);
    setStop(stopInfo);
  }



  if (loop()){ //
    atuoSingleLoop();
  }


  return stopLoop;
}


let arrayPropertyDescriptors = {


  /**
   * multipleLoop(option)=> stopLoop()
   * 
   * @param option : {loopCall,complete,stepComplete,thisValue,step,delay}   
   * @property option.loopCall : (currentValue,index,stepCount,arr)=>stopInfo : any   currentValue : Item  index   index : number   index0 stepCount : number   arr:Array   stopInfo : any 
   * @property option.stepComplete  : (index,stepCount,arr)=>stopInfo : any    index : number   index0 stepCount : number   arr: Array   stopInfo : any 
   * @property option.complete : (stopInfo,index,stepCount,arr)=>Void   stopInfo : any   index : number   index-1  stepCount : number   arr: Array 
   * @property option.thisValue ? : any    loopCallcompletestepComplete  this
   * @property option.step ? : number      50  
   * @property option.delay ? : Timestamp    0  
   * @returns stopLoop : (stopInfo)=>Void      stopInfo : any 
   */
  multipleLoop: {
    enumerable: false,
    value: function ({loopCall,complete,stepComplete,thisValue,step,delay}) {

      if (thisValue){
        thisValue = this;
      }

      let loopOpt = {
        loopCall:(index,stepCount,total)=> {
          return loopCall.call(thisValue,this[index],index,stepCount,this);
        },
        total:this.length,
        step:step,
        delay:delay
      };

      if (complete){
        loopOpt.complete = (stopInfo,index,stepCount,total)=>{
          return complete.call(thisValue,stopInfo,index,stepCount,this);
        };
      }

      if (stepComplete){
        loopOpt.stepComplete = (index,stepCount,total)=>{
          return stepComplete.call(thisValue,index,stepCount,this);
        };
      }


      return multipleLoop(loopOpt);
    }
  }

};

  Object.defineProperties(Array.prototype, arrayPropertyDescriptors);



