/**
 * safelyIterate(iterable,operation, thisValue)
 *  iterable   for...of safelyIterate  operation 
 * @param iterable : Iterable    
 * @param operation : (currentValue,currentIndex,iterable)=>boolean | undefined      
 * @param thisValue ? : any    iterable  operation  this 
 * @returns [Item]   operation 
 *
 *
 * operation(currentValue,currentIndex,iterable)=>boolean | undefined
 * @param currentValue : any    operation 
 * @param currentIndex : number     currentValue  iterable  
 * @param iterable : Iterable    iterable 
 * @returns boolean | undefined   currentValue 
 *
 */
export function safelyIterate(iterable,operation, thisValue) {

  if (thisValue == undefined) {
    thisValue = iterable;
  }

  let arrayCopy = [];

  for (let value of iterable){
    arrayCopy.push(value);
  }

  let filterItem = arrayCopy.filter(function (currentValue) {
    let currentIndex = this.indexOf(currentValue);
    operation.call(thisValue, currentValue, currentIndex, iterable);
  },arrayCopy);


  return filterItem;

}






