/**
 *  inst 
 * @param inst : any
 * @returns Type    inst 
 *
 *
 *
 * 
 * 
 * undefined undefined
 * null  null
 *   :    
 *
 */
export function getTypeOf(inst) {
  var typeInfo = inst;
  if (inst != null){
    typeInfo = inst.constructor;
  }

  return typeInfo;

}





/**
 *  inst 
 * @param inst : any
 * @returns string    inst 
 *
 *
 *
 * 
 * 
 * undefined "undefined"
 * null  "null"
 *   :     
 *
 */
export function getTypeStringOf(inst) {

  switch (inst){
    case undefined:{
      var typeStr = "undefined";
      break;
    }

    case null:{
      typeStr = "null";
      break;
    }

    default:{
      typeStr = inst.constructor.name;
    }
  }

  return typeStr;

}



