/**
 * 
 *
 * @param obj : Object   
 * @param prop : string   
 * @param options ?: {ready ?:string,noEvent ?:boolean,event ?:string,newValueKey ?:string,oldValueKey ?:string,getDefault ?:(thisValue)=>PropValue}     
 * @property  ready ?:string    prop + "Ready" ready
 * @property  noEvent ?:boolean    false  prop 
 * @property  event ?:string    prop +  "Change" prop
 * @property  newValueKey ?:string    "value" prop
 * @property  oldValueKey ?:string    "oldValue" prop
 * @property  getDefault ?:(thisValue)=>PropValue     prop  prop   
 *
 *
 * @returns obj : Object  
 */
export function defineListenableProperty(obj,prop,options){
  let {ready:readyName = prop + "Ready",noEvent,event:eventName = prop +  "Change",newValueKey = "value",oldValueKey = "oldValue",getDefault} = options || {};
  let priReadyName = "_" + readyName;
  let priProp = "_" + prop;



  /**
   *  obj  ready  promise  ready  ready 
   */
  Object.defineProperty(obj, readyName, {
    configurable:true,
    enumerable:true,
    get: function () {
      let _this = this || window;
      if (!_this[priReadyName]) {
        let propValue = _this[priProp];
        if (propValue){
          _this[priReadyName] = Promise.resolve(propValue);
        }else {
          _this[priReadyName] = createControllablePromise();
        }

      }
      return _this[priReadyName];
    },
    set:function (newValue) {
      let _this = this || window;
      _this[priReadyName] = newValue;
    }
  });



  // prop  get 
  if (getDefault){

    var propGetter = function () {
      let _this = this || window;
      if (!_this[priProp] && getDefault) {
        _this[prop] = getDefault.call(_this,_this);
      }
      return _this[priProp];
    }

  }else {

    propGetter = function () {
      let _this = this || window;
      return _this[priProp];
    }

  }


  // prop  set 

  if (noEvent){


    var propSetter =  function (newValue) {
      let _this = this || window;
      if (newValue && newValue !== _this[priProp]) {


        let oldValue = _this[priProp];
        _this[priProp] = newValue;

        let httpReady = _this[readyName];
        if (httpReady.resolve) {
          httpReady.resolve(newValue);
        }else {
          _this[readyName] = Promise.resolve(newValue);
        }

      }
    }

  }else {


    propSetter =  function (newValue) {
      let _this = this || window;
      if (newValue && newValue !== _this[priProp]) {


        let oldValue = _this[priProp];
        _this[priProp] = newValue;

        let httpReady = _this[readyName];
        if (httpReady.resolve) {
          httpReady.resolve(newValue);
        }else {
          _this[readyName] = Promise.resolve(newValue);
        }

        // change 
        let change = new Event(eventName, {"bubbles": true});
        change[newValueKey] = newValue;
        change[oldValueKey] = oldValue;
        window.dispatchEvent(change);


      }
    }

  }





  /**
   *  obj   prop  prop
   *
   * 
   *  prop  window  change 
   *  event[newValueKey] 
   *  event[oldValueKey] 
   */
  Object.defineProperty(obj, prop, {
    configurable:true,
    enumerable:true,
    get: propGetter,
    set: propSetter
  });


  return obj;
}





/**
 * 
 *
 * 1defineListenableProperties(obj,propOptions)
 * @param obj : Object   
 * @param propOptions : {propName:options}      
 * @returns obj : Object  
 *
 *
 * 2defineListenableProperties(obj,propArray,options)
 * @param obj : Object   
 * @param propArray : [string]   
 * @param options ?: {ready ?:string,noEvent ?:boolean,event ?:string,newValueKey ?:string,oldValueKey ?:string,getDefault ?:(thisValue)=>PropValue}     
 *
 * @returns obj : Object  
 */
export function defineListenableProperties(obj,props,options){

  var propsObj = props;
  if (Array.isArray(props)) {
    propsObj = props.reduce(function (total,propName) {
      total[propName] = options;
      return total;
    },{});
  }


  Object.keys(propsObj).forEach(function (propName) {
    let propOpts = propsObj[propName];
    defineListenableProperty(obj,propName,propOpts);
  });

  return obj;
}


/**
 * defineListenablePropertyGetter(obj, prop, getDefault, asGetter)
 *  getter   getDefault  getDefault  getter 
 * @param obj : Object   
 * @param prop : string   
 * @param  getDefault ?:(thisValue)=>PropValue     prop  prop   
 * @param asGetter ?: boolean     getDefault  getter
 */
export function defineListenablePropertyGetter(obj, prop, getDefault, asGetter) {

  let priProp = "_" + prop;

  if (asGetter) {
    var propGetter = getDefault;
  } else if (getDefault) {

    propGetter = function () {
      let _this = this || window;
      if (!_this[priProp] && getDefault) {
        _this[prop] = getDefault.call(_this, _this);
      }
      return _this[priProp];
    }

  } else {

    propGetter = function () {
      let _this = this || window;
      return _this[priProp];
    }

  }


  Object.defineProperty(obj, prop, {
    configurable:true,
    enumerable:true,
    get: propGetter
  });
}


/**
 * 
 * @returns {string}
 */
export function createUniqueIdentifier() {
  var currentDate = new Date();
  return currentDate.getTime().toString() + Math.random();
}

















/**
 * createControllablePromise(executor ?:(resolve, reject)=>Void)
 *  Promise  Status  Promise  Promise    resolverejectclearAdditions  Promise resolverejectclearAdditions  clearAdditions    resolverejectclearAdditions 
 *
 * @param executor ?: (resolve, reject)=>Void    executor resolve  reject  Promiseexecutor 
 * @param statusCompletesImmediately ?: boolean   true  executor  resolve  reject  executor  clearAdditions    promise 
 * @returns Promise    resolvereject  clearAdditions   Promise    resolve  reject  Promise  clearAdditions   resolverejectclearAdditions  clearAdditions  resolvereject  clearAdditions  
 */
export function createControllablePromise(executor,statusCompletesImmediately) {
  let executorIsvalid = typeof executor == "function";
  if (executorIsvalid && statusCompletesImmediately == undefined){
    statusCompletesImmediately = true
  }


  var oriFuns = {};

  function clearAdditions() {
    caPromise.resolve = undefined;
    caPromise.reject = undefined;
    caPromise.clearAdditions = undefined;
  }

  function resolveFun(value) {
    clearAdditions();
    oriFuns.resolve(value);
  }

  function rejectFun(reason) {
    clearAdditions();
    oriFuns.reject(reason);
  }


  var caPromise = new Promise(function (resolve, reject) {
    oriFuns.resolve = resolve;
    oriFuns.reject = reject;
    if (executorIsvalid) {
      if (statusCompletesImmediately) {
        setTimeout(executor,0,resolveFun, rejectFun);
      }else {
        return executor(resolveFun, rejectFun);
      }
    }
  });

  caPromise.clearAdditions = clearAdditions;
  caPromise.resolve = resolveFun;
  caPromise.reject = rejectFun;


  return caPromise;
}
