
// JSON  extendJSON 
var extendJSON = {
  extended:true,  // JSON  extendJSON 


  /**
   * 
   * @param text : string     JSON 
   * @param reviver ?: function    
   * @returns {parsed: boolean, result: string | JSONObject }    parsed  result  JSON text
   */
  safelyParse: function (text, reviver) {
    let parseInfo = {
      parsed: true,
      result: text
    };

    try {
      parseInfo.result = JSON.parse(text, reviver);
    } catch (e) {
      parseInfo.parsed = false;
    }

    return parseInfo;
  },


  /**
   * JSON
   * @param target : any     JSON 
   * @param replacer ? : (key,vlaue)=>vlaue |  Array     JSON null JSON 
   * @param space ?  pretty-print101()null
   * @returns string   JSON
   *
   *
   *
   * 
   *  target  JSON 
   */
  depthStringify:function(target, replacer , space){
    let newTarget = Object.keys(target).reduce((total,key)=>{
      let prop = target[key];
      if (typeof prop == "object"){
        prop = this.depthStringify(prop,replacer , space);
      }
      total[key] = prop;
      return total;
    },{});

    return JSON.stringify(newTarget,replacer , space)
  },


  /**
   *  depthStringify 
   * @param text
   * @param reviver
   * @returns any
   */
  depthParse:function(text, reviver){
    let result = JSON.correctParse(text, reviver);
    if (typeof result == "object"){
      Object.keys(result).forEach((key)=> {
        let prop = result[key];
        prop = this.depthParse(prop,reviver);
        result[key] = prop;
      });
    }

    return result;
  },


  /**
   *  text JSON
   * @returns boolean
   */
  isJSONString:function (text) {

    try {
      var parsed = JSON.parse(text);
      var isJSON = parsed !== text;
    } catch (e) {
      isJSON = false;
    }

    return isJSON;

  },


  /**
   * correctParse(text,reviver)
   *  text  JSON text
   * @param text :  string   
   * @returns any
   */
  correctParse:function (text,reviver) {

    try {
      var parsed = JSON.parse(text, reviver);
      var isJSON = parsed !== text;
    } catch (e) {
      isJSON = false;
    }

    if (!isJSON){
      parsed = text;
    }

    return parsed;

  }





};







var oriJSON = globalThis.JSON ;

/*
* 
*  JSON 
* */
Object.defineProperty(globalThis, "JSON", {
  get: function () {
    return globalThis._JSON;
  },

  set: function (newValue) {
    if (!newValue.extended) {
      Object.assign(newValue,extendJSON);
    }
    globalThis._JSON = newValue;
  }
});

globalThis.JSON = oriJSON ;
