/**
 * paramsPropParse ; JSON
 */
function _paramsPropJSONParse(value) {
  return JSON.correctParse(value);
}



/**
 * paramsPropStringify JSON
 */
function _paramsPropJSONStringify(value,key,params) {
    if (typeof value == "object"){
      value = JSON.stringify(value);
    }
    return value;
}




/**
 * url UrlUtils  UrlUtils  url 
 * 
 * -  URL  URL 
 * -  searchParams  hashParams  JSON 
 *
 * @param urlStr : string   urlurl
 * @param paramsPropParse ? : (value,key,queryString)=> value     URL  url  , JSON 
 * @param paramsPropStringify ? : (value,key,params)=> string     , JSON
 * @returns urlUtils : UrlUtils
 *
  UrlUtils 
 href:string,    // URL  DOMString
 url:string,   //searchhase url
 origin:string,  // DOMString
 protocol:string,  // URL  DOMString ':'
 host:string,  // URL ':' DOMString
 hostname:string,  // URL  DOMString
 port:string,    // URL  DOMString
 pathname:string,  //URL 
 pathList:Array,  //url
 absPath:boolean, //pathname 
 search:string,    // '?'  URL  DOMString
 searchParams:Object,  //search
 hash:string, // '#'  URL  DOMString
 hashSearch:string, // hash
 hashParams:Object,   //hash
 hashUrlUtils:UrlUtils,  // hash  url  urlUtils
 params:Object,    //url searchParams  hashParams
 *
 */
export function parseUrl(urlStr,paramsPropParse,paramsPropStringify){
  var protocolRegExp;
  protocolRegExp = /^\w+(?=:\/\/)/;
  var hostRegExp = /([^./:\s]+(?:\.[^./:\s]+)+)(?::(\d+))?|([^./:\s]+)(?::(\d+))/;

  if (!paramsPropParse){
    paramsPropParse = _paramsPropJSONParse;
  }


  if (!paramsPropStringify){
    paramsPropStringify = _paramsPropJSONStringify;
  }

  var href = urlStr.trim();
  var {url,search,hash} = splitURLByQueryString(href);


  // protocol
  if (url){
    var hostUrl = url;
    var protoList = url.split("://");
    if (protoList.length > 1){
      var protocol = protoList[0];
      hostUrl = protoList[1];
    }
  }





  // host hostname port pathname  pathList absPath
  if (hostUrl){
    var hostList = hostUrl.split("/");

    if (hostList.length > 1){

      var hostItem0 = hostList[0];

      if (/^\.+$/.test(hostItem0)){ // .
        var pathname = hostUrl;
        var pathList = hostList.slice(1);
        var absPath = false;     //
      }else if (hostItem0) {
        var hostRegRes = hostItem0.match(hostRegExp);
        if (hostRegRes){  /// host 
          var host = hostRegRes[0];
          var hostname = hostRegRes[1] || hostRegRes[3];
          var port = hostRegRes[2] || hostRegRes[4];
          pathList = hostList.slice(1);
          pathname = "/" + pathList.join("/");
          absPath = true;
        }else {
          absPath = false;
          pathList = hostList;
          pathname = hostUrl;
        }

      }else { /// hostUrl 
        pathList =  hostList.slice(1);
        pathname = hostUrl;
        absPath = true;
      }



    }else { // hostUrl  /  hostUrl 
      hostRegRes = hostUrl.match(hostRegExp);
      if (hostRegRes) {
        host = hostUrl;
        hostname = hostRegRes[1];
        port = hostRegRes[2];
        absPath = true;
      }else {
        pathname = hostUrl;
        pathList = hostList;
        absPath = false;
      }

    }

  }




  // origin
  if (host) {
    if (protocol){
      var origin = protocol + "://" + host;
    } else {
      origin = host
    }

  }



  // search  params
  if (search) {
    var searchParams = parseQueryString(search,paramsPropParse);
  }

  // hash hashParams
  if (hash){
    var hashParamStr = hash.replace(/^#+/,"");
    var hashUrlUtils = parseUrl(hashParamStr,paramsPropParse,paramsPropStringify);
    var hashSearch = hashUrlUtils.search;
    var hashParams = hashUrlUtils.params;
  }


  // params 
  if  (searchParams || hashParams){
    var params = {};
    if (searchParams){
      Object.assign(params,searchParams);
    }

    if (hashParams){
      Object.assign(params,hashParams);
    }
  }




  // UrlUtils
  var urlUtils = {
    href:href,    // URL  DOMString
    url:url,   //searchhase url
    origin:origin,  // DOMString
    protocol:protocol,  // URL  DOMString ':'
    host:host,  // URL ':' DOMString
    hostname:hostname,  // URL  DOMString
    port:port,    // URL  DOMString
    pathname:pathname,  //URL 
    pathList:pathList,  //url
    absPath:absPath, //pathname 
    search:search,    // '?'  URL  DOMString
    searchParams:searchParams,  //search
    hash:hash, // '#'  URL  DOMString
    hashSearch:hashSearch, // hash
    hashParams:hashParams,   //hash
    hashUrlUtils:hashUrlUtils,  // hash  url  urlUtils
    params:params,    //url searchParams  hashParams
  };


  // UrlUtils 
  var simpUrlUtils = Object.keys(urlUtils).reduce(function (total, key) {
    var value = urlUtils[key];
    if (value != null){
      total[key] = value;
    }
    return total;
  },{});


  return simpUrlUtils;
}






/**
 *   UrlUtils  url  UrlUtils  url 
 *
 * @param urlUtils : UrlUtils   urlurl
 * @param paramsPropStringify ? : (value,key,params)=> string     , JSON
 * @param paramsPropParse ? : (value,key,queryString)=> value     URL  url  , JSON 
 * @returns urlStr : string
 *
 *
 * 
 * -  URL  URL 
 * -  searchParams  hashParams  JSON 
 * -  urlUtils   hash   urlUtils  hashUrlUtils 
 * -  urlUtils url urlUtils  href  host  host  href  host 
 *
 *
 *
 *
 * urlUtils  parseUrl addSearchaddSearchParamsaddHashSearchaddHashParams 
 * urlUtils 
 addSearch: string   // search addSearchParams
 addSearchParams: Object  //  search  addSearch
 addHashSearch: string   // hashSearch  addHashParams
 addHashParams: Object  //  hashSearch  addHashSearch

 href:string,    // URL  DOMString
 url:string,   //searchhase url
 origin:string,  // DOMString
 protocol:string,  // URL  DOMString ':'
 host:string,  // URL ':' DOMString
 hostname:string,  // URL  DOMString
 port:string,    // URL  DOMString
 pathname:string,  //URL 
 pathList:Array,  //url
 absPath:boolean, //pathname 
 search:string,    // '?'  URL  DOMString searchParams
 searchParams:Object,  //search  params   hashParams   search
 hash:string, // '#'  URL  DOMString  hashUrlUtils.href 
 hashSearch:string, // hash  hashParams
 hashParams:Object,   //hash   searchParams  params   searchParams   hashSearch hashSearch  hashParams    hashUrlUtils.searchParams 
 hashUrlUtils:UrlUtils,  // hash  url  urlUtils  hashSearch  hashParams    hashUrlUtils.searchParams 
 params:Object,    //url
 *
 *
 */
export function stringifyUrl(urlUtils,paramsPropStringify,paramsPropParse){
  var urlStr = "";

  if (!paramsPropStringify){
    paramsPropStringify = _paramsPropJSONStringify;
  }



  if (!paramsPropParse){
    paramsPropParse = _paramsPropJSONParse;
  }


  var href = urlUtils.href || urlUtils.url || urlUtils.origin;
  if (href){
    var hrefUrlUtils = parseUrl(href,paramsPropParse,paramsPropStringify);
    urlUtils = Object.assign(hrefUrlUtils,urlUtils)
  }


  // protocol
  var protocol = urlUtils.protocol;
  if (protocol){
    urlStr = protocol + "://";
  }

  // host
  var host = urlUtils.host;
  var hostname = urlUtils.hostname;
  if  (hostname){
    host = hostname;
    var port = urlUtils.port;
    if (port){
      host += ":" + port;
    }
  }


  if (host){
    urlStr += host ;
  }



  // pathname
  var pathname = urlUtils.pathname;
  var pathList = urlUtils.pathList;
  var absPath = urlUtils.absPath;
  if (!pathname && pathList) {
    pathname = "/" + pathList.join("/");
  }
  if (pathname){
    pathname = pathname.trim();
    if (urlStr || absPath === true) {
      pathname = pathname.replace(/^[./]*/,"/");
    }
    urlStr += pathname;
  }








  // addSearch  addSearchParams
  var search = urlUtils.search;
  var searchParams = urlUtils.searchParams;

  if (!searchParams && search){
    searchParams = parseQueryString(search,paramsPropParse);
  }

  if (searchParams){
    var mergedSearchParams = Object.assign({},searchParams);
  }

  var params = urlUtils.params;
  if(!searchParams && params){
    var excludeParams = urlUtils.hashParams || {};
    var excludeKeys = Object.keys(excludeParams)
    searchParams = params.filterProperty(function (key) {
      return !excludeKeys.includes(key);
    });

    if (searchParams.noKeys){
      searchParams = null;
    }
  }

  var addSearchParams = urlUtils.addSearchParams;
  var addSearch = urlUtils.addSearch;
  if (!addSearchParams && addSearch){
    addSearchParams = parseQueryString(addSearch,paramsPropParse);
  }

  if (searchParams && addSearchParams){
    searchParams = Object.assign({},searchParams,addSearchParams);
  }else if (addSearchParams){
    searchParams = addSearchParams;
  }




  // search
  if (searchParams && !searchParams.noKeys){
    search = queryStringify(searchParams,true,paramsPropStringify);
  }else {
    search = null;
  }

  if (search){
    urlStr += search;
  }



  // addHashSearch  addHashParams
  var hashSearch = urlUtils.hashSearch;
  var hashParams = urlUtils.hashParams;

  if (!hashParams && hashSearch){
    hashParams = parseQueryString(hashSearch,paramsPropParse);
  }

  if(!hashParams && mergedSearchParams && params){
    excludeKeys = Object.keys(mergedSearchParams)
    hashParams = params.filterProperty(function (key) {
      return !excludeKeys.includes(key);
    });

    if (hashParams.noKeys){
      hashParams = null;
    }
  }



  var addHashParams = urlUtils.addHashParams;
  var addHashSearch = urlUtils.addHashSearch;
  if (!addHashParams && addHashSearch){
    addHashParams = parseQueryString(addHashSearch,paramsPropParse);
  }

  if (hashParams && addHashParams){
    hashParams = Object.assign({},hashParams,addHashParams);
  }else if (addHashParams){
    hashParams = addHashParams;
  }




  // hash
  var hash = urlUtils.hash;
  var hashUrlUtils = urlUtils.hashUrlUtils;


  if (hashUrlUtils || hashParams) {

    if (hashUrlUtils){
      hashUrlUtils = Object.assign({},hashUrlUtils)
    }else {
      hashUrlUtils = {};
    }

    if (hash){
      hash = hash.replace(/^#+/,"");
      hashUrlUtils.href = hash;
    }

    if (hashParams) {
      hashUrlUtils.searchParams = hashParams;
    }

    if (!hashUrlUtils.noKeys){
      hash = stringifyUrl(hashUrlUtils,paramsPropStringify,paramsPropParse);
    }

  }


  if (hash) {
    hash = hash.replace(/^#*/,"#");
    urlStr += hash;
  }


  return urlStr;
}








/**
 * url urlsearch  hash 
 * @param urlStr
 * @returns {url: string, search: string, hash: string}   url search  hash  search  "?", hash  "#"
 *
 *   URL  urlStr    ##   
 */
export function splitURLByQueryString(urlStr) {

  var preIndex = urlStr.indexOf("?");
  var anrIndex = urlStr.indexOf("#");

  var urlQS = urlStr;
  var hash = undefined;
  if (anrIndex >= 0){
    urlQS = urlStr.substring(0,anrIndex);
    hash = urlStr.substring(anrIndex);
  }

  var search = undefined;
  var url = urlQS;
  if (preIndex >= 0) {
    search = urlQS.substring(preIndex);
    url = urlQS.substring(0,preIndex);
  }

  return {url,search,hash};
}











