export function parseJSONQueryStrObjProperty(queryObj) {
  return Object.entries(queryObj).reduce(function (total, kvList) {
    let key = kvList[0];
    let jsonStr = decodeURIComponent(kvList[1]);
    total[key] = JSON.correctParse(kvList[1]);

    return total;
  }, {});

}



