// Input




/**
 * ipnut
 * 1. input
 * 2. input
 *
 * 
 * inputinput
 * touchstartclick 
 *
 *
 * focusInputOnTargetOfEvent(targetTests,eventName,excludeInput)
 *  eventName input  input
 * @param targetTests : Array< targetFun | ElementMatchOption>    
 * @param eventName ? : string     "click" 
 * @param excludeInput ? :  boolean  false ; input input
 *
 *
 * targetFun : (targetElement)=>boolean   
 * elementMatchOption : {tag:string, id:string, class:string | Array,  selector:string}   
 *
 * elementMatchOptionoption
 * option 
 * tag ? : string    
 * id ? : string   id
 * class ? : string | Array  class    className  
 * selector : string    css
 *
 */
export function focusInputOnTargetOfEvent(targetTests,eventName,excludeInput) {

  if (!Array.isArray(targetTests)) {
    targetTests = [targetTests];
  }

  if (!eventName){
    eventName = "click"
  }

  /**
   *  testArr   target  test 
   * @param target
   * @param testArr
   * @returns boolean
   */
  function testTarget(target,testArr) {
    return testArr.some(function (test) {
      if (typeof test == "function") {
        return test(target);
      }else {
        return target.isMatchOption(test);
      }
    });
  }


  document.addEventListener(eventName,function (event) {

    var target = event.target;

    if (testTarget(target,targetTests)) {


      if (target.localName == "input") {

        if (!excludeInput) {
          target.focus();
        }

      }else {

        var inputDom = target.getElementsByTagName("input")[0];
        if (inputDom) {
          inputDom.focus();
        }

      }


    }

  });

}




