
let propertyDescriptors = {



  /**
   * 1:
   * distanceSort(num1,num2 ,...)
   *  num   
   * @param num : number    
   * @returns [number]      
   *
   *
   * 2:
   * distanceSort(nums)
   *  nums   
   * @param nums : [number]    
   * @returns [number]      
   */
  distanceSort:{
    enumerable:false,
    value:function(...nums){
      return Math.distanceSort(this,...nums);
    }
  },



  /**
   * 1:
   * nearest(num1,num2 ,...)
   *   
   * @param num : number    
   * @returns [number]      
   *
   *
   * 2:
   * nearest(nums)
   *   
   * @param nums : [number]    
   * @returns [number]      
   */
  nearest:{
    enumerable:false,
    value:function(...nums){
      return Math.nearest(this,...nums);
    }
  },




  /**
   * 1:
   * farthest(num1,num2 ,...)
   *   
   * @param num : number    
   * @returns [number]      
   *
   *
   * 2:
   * farthest(nums)
   *   
   * @param nums : [number]    
   * @returns [number]      
   */
  farthest:{
    enumerable:false,
    value:function(...nums){
      return Math.farthest(this,...nums);
    }
  },



};






Object.defineProperties(Number.prototype,propertyDescriptors);







//


if (!Number.isInteger) {
  Number.isInteger = Number.isInteger || function(value) {
    return typeof value === "number" && isFinite(value) && Math.floor(value) === value;
  };
}


