/**
 * 
 *
 * @param keyName : DOMString  
 * @param anyItem : any      
 * @param replacer ? : Function | Array<Function>    JSON null
 * @param space ? : number | string    pretty-print101()null
 */
Storage.prototype.setAnyItem = function (keyName, anyItem, replacer, space) {
  let jsonStr = JSON.stringify(anyItem, replacer, space);
  return this.setItem(keyName, jsonStr);
}


/**
 * key name 
 *
 * @param keyName : DOMString     DOMString
 * @param reviver ? : Function    
 * @returns any
 */
Storage.prototype.getParsedItem = function (keyName, reviver) {
  let jsonStr = this.getItem(keyName);
  return JSON.correctParse(jsonStr, reviver);
}
